/* ----------- */

html, body, .mainContainer
{
    height: 100%;
    margin: 0;
    padding: 0px;
    /* overflow: hidden; */
}
body {
    background-image: url("../public/assets/xivheaderbackground.png"), url("../public/assets/xivbackgroundgray.png");
    background-repeat: repeat-x, repeat;
}
#root {
    height: 100%;
    display: flex;
    justify-content: center;
}
.mainContainer {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 50px;

    position:absolute;
    height:auto;
    bottom:0;
    top:0;
    left:0;
    right:0;
    /* margin-top:20px; */
    margin-bottom:20px;
    margin-right:80px;
    margin-left:80px;

}
#canvas
{
    max-width: 800px;
    /* width: 100%; */
    /* max-height: 800px; */
    /* height: 100%; */
    background: white;
    border-radius: 16px;
    cursor: pointer;
}

.pageTitle {
    grid-area: title;
}
#canvasBorder {
    grid-area: canvas;
    width: 100%;
}
#leftWindow {
    grid-area: details;
}
.mainGrid {
    padding-right: 16px;
    grid-gap: 8px;
    height: 100%;
    min-height: 700px;
    justify-items: start;
    align-content: start;

    display: grid;
    grid-template-columns: 330px 600px;
    grid-template-rows: auto minmax(600px, 1fr);
    grid-template-areas:
    "title title"
    "details canvas"
}

#canvasBorder {
    border-style: solid;
    border-width: 1px;
    border-image-source: url("../public/assets/ffxivuiframe.png");
    border-image-width: 23px;
    border-image-slice: 23;
    padding: 6px; /* 10px */
    position: relative;
}
.windowContainer {
    /* max-height: 90%; */
    display: flex;
    /*
    align-items: center;
    justify-content: center;

     */
    margin: 23px;
    /* position: absolute; */
}
.window {
    /* border-image: url("ffxivuiframe.png") 23 fill / 23px / 23px space; */
    /*border-style: solid; */

    /*border-style: solid; */
    /* border: 0px solid; */
    border-style: solid;
    border-width: 1px;
    border-image-source: url("../public/assets/ffxivuiframe.png");
    border-image-width: 23px;
    border-image-slice: 23;
    /* border-image-outset: 23; */
    /* border-width: 23px; */

    /* */
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;



    /* */
    padding: 23px;
    padding-right: 36px;
    border-radius: 23px;
    filter: drop-shadow(0px 1px 3px #222);
    background-image: url("../public/assets/xivheaderbackground.png"), url("../public/assets/xivbackgroundgray.png");
    background-repeat: repeat-x, repeat;
    color: rgb(206, 206, 206);
    font-family: sans-serif;
}
img {
    position: absolute;
}
#mapWindow {
    position: absolute;
    top: 0;
}
.leftWindow {
    width: 330px;
    margin: 0;
    margin-right: 23px;
}
.leftWindow .window {
    width: 100%;
}
.flexRow {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    height: 100%;
    /*
    justify-content: space-around;
    flex: 1;

     */
}
.flexMain {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 100%;
}
.window {
    /* overflow-y: scroll; */
    overflow-wrap: anywhere; /* CHANGE ME */
    /* padding-right: 8px; */
}
h1 {
    padding: 0;
    margin: 0;
    filter: drop-shadow(1px 1px 1px black);
}
.wardName {

}
button.plotTitleClickable {
    cursor: pointer;
    border: 0;
    background: none;
    font-size: 16px;
    color: rgb(206, 206, 206);
    position: relative;
    filter: drop-shadow(1px 1px 1px black);
    width: 100%;
    text-align: left;
}
button.plotTitleClickable:active {
    top: 1px;
}
.smallArrowButton {
    background: rgb(86,85,86);
    background: linear-gradient(0deg, rgba(86,85,86,1) 45%, rgba(59,60,59,1) 55%);
    position: absolute;
    width: 20px;
    height: 20px;
    border-radius: 13px;
    top: -3px;
    border: 1px solid #716f71;
    /*filter: drop-shadow(0px 1px 1px black);*/

}
.smallArrowButton:after {
    content: '›'; /* '❯'; */
    position: relative;
    top: -6px;
    padding-left: 5px;
    font-size: 28px;
    line-height: 1;
    font-weight: bold;
}
.window p {
    filter: drop-shadow(1px 1px 1px black);
    margin: 0;
}
.detailRow {
    margin-bottom: 16px;
    padding: 5px;
}
.detailRow hr {
    margin-top: 1px;
    margin-bottom: 8px;
}
.detailRow.selected {
    background: radial-gradient(rgba(255,255,255,0.1) 41%, rgba(255, 255, 255, 0.0) 68%);
}
.rowTitle {
    color: rgb(163, 163, 163);
    padding-left: 28px;
    padding-bottom: 10px;
    position: relative;
    top: -2px;
}
.rowTitleRight {
    font-weight: bold;
    position: relative;
    top: -2px;
}
.rowText {
    color: rgb(239, 226, 199);
    margin-left: 16px;
}
.rowTags {
    margin-left: 16px;
    font-size: 13px;
}

.scrollArea {
    overflow-y: scroll;
    overflow-x: hidden;
    height: 100%;
    padding-right: 8px;
}

/* Works on Firefox */
* {
    scrollbar-width: thin;
    scrollbar-color: gray transparent;
}

/* Works on Chrome, Edge, and Safari */
*::-webkit-scrollbar {
    width: 12px;
}

*::-webkit-scrollbar-track {
    background: transparent;
}

*::-webkit-scrollbar-thumb {
    background-color: gray;
    border-radius: 20px;
    border: none;
}